@font-face {
  font-family: 'Muller';
  src: local('MullerLight'),
    url('../fonts/MullerLight.eot') format('embedded-opentype'),
    url('../fonts/MullerLight.woff2') format('woff'),
    url('../fonts/MullerLight.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}


